<template>
    <section>
        <div class="theme-primary partition p-actions-expand overflow-visible">
            <template v-for="(getrow,getindex) in dynamicArray" v-if="isHiding">

                <span class="p-actions float-right mydbsize" v-if="getindex == 0">
                    <a id="aFillTimeEntry" class="p-action-btn text-white"
                        @click="createMultiRows(getrow.modeluserid.length - 1)" href="javascript:;" title=""
                        data-toggle-tooltip="tooltip" data-original-title="Click here to fill time entry."><i
                            class="fa fa-clock-o pr-2"></i>
                        <br>
                        <span>Fill Time Entry</span>
                    </a>
                </span>
            </template>
        </div>

        <div class="row">
            <div class="col-12 mb-2">
                <div class="mr-2 d-inline-block">
                    <span class="legends_profile colorboxsquare"></span>Overlap Entry
                </div>
                <div class="mr-2 d-inline-block">
                    <span class="legends_high colorboxsquare"></span>Future Entry
                </div>
                <div class="mr-2 d-inline-block">
                    <span class="legends_low colorboxsquare"></span>Saved Successfully
                </div>
                <div class="mr-2 d-inline-block">
                    <span class="legends_medium colorboxsquare"></span>Exceed One Day Limit
                </div>
            </div>

            <!-- <div class="listing py-3 px-3">
                    <dynamic-form lang="en"  :buttons="false" :schema="FormSchema" v-on:OnSubmit="onSubmit"></dynamic-form>
            </div> -->



            <div id="bulkentryMulti" class="table-responsive" style="min-height:400px;">
                <table id="tblbulkentry" class="table table-bordered dt-responsive nowrap mt-0" style="min-width:1200px;">
                    <thead class="thead-bg">
                        <tr>
                            <th style="min-width: 230px;"><span>Project</span></th>
                            <th style="min-width: 350px;"><span>Task</span></th>
                            <th style="min-width: 290px;"><span>User</span></th>
                            <th style="min-width: 300px;"><span>Description</span></th>
                            <th style="min-width: 150px;"><span> Is Billable</span></th>
                            <th style="min-width: 200px;"><span>Date</span></th>
                            <th style="min-width: 180px;" class="d-none"><span>Shift</span></th>
                            <th style="min-width: 200px;"><span>Total Hours</span></th>
                            <th class="text-center d-none" style="min-width: 200px;"><span>Start Time</span></th>
                            <th class="text-center d-none" style="min-width: 200px;"><span>Stop Time</span></th>
                            <th class="text-center" style="min-width: 92px;"><span>Actions</span></th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr class="visible" :id="'trdata'+index" crtrwid="1001" v-for="(item,index) in dynamicArray" :key="index">
                            <td class="align-middle">
                                <select name="ddlProjectsBulk" v-model="item.modelprojectid" id="ddlProjectsBulk"
                                    class="form-control" v-on:change="ddlProjectschange(modelprojectid);getUserByProjectIdAndTaskId(modelprojectid,0);"
                                    style="white-space: nowrap;padding-right: 20px;">
                                    <option value="">No Project</option>
                                    <option v-for="item in projects" :value="item.projectId">{{ item.projectName }}</option>
                                </select>
                            </td>
                            <td class="align-middle" style="overflow: visible !important;">
                                <div class="form-group">
                                    <div class="w-100 float-left" id="ddlTaskUnassignedN">
                                        <select id="ddlTaskN" class="form-control" v-model="item.modeltaskid">
                                            <option value="">Unassigned Task</option>
                                            <option v-for="taskitem in TaskData" :value="taskitem.value">{{ taskitem.text }}
                                            </option>
                                        </select>
                                    </div>
                                    <div class="w-100 timersc_ddl tt-select-task" id="ddlTaskSelectTaskN"
                                        style="display: none;">
                                        <div id="ddlTask" class="ht212_hs" name="ddlTask"
                                        v-on:change="getUserByProjectIdAndTaskId(modelprojectid,modeltaskid);"></div>
                                        <input type="hidden" name="Tasks" id="hdnddltask" value="">
                                        <input type="hidden" name="isbillable" id="hdnisbillable" value="">
                                    </div>
                                </div>
                            </td>
                            <td class="align-middle" style="overflow: visible !important;">

                                <multiselect  
                                        v-model="item.modeluserid" 
                                        :searchable="true" 
                                        :options="options" 
                                        :multiple="true" 
                                        placeholder="Select" 
                                        label="name" 
                                        track-by="value"
                                        v-validate="required"  >  
                                </multiselect>
                            </td>
                            <td class="align-middle">
                                <input id="taskDescription" v-model="item.modeldescription" maxlength="1000"
                                    class="form-control" name="name" type="text"
                                    onkeypress="javascript:return NoSpecialCharac(event,this);" autocomplete="off">
                                <span id="taskDescriptionError" class="text-danger"></span>
                            </td>
                            <td class="align-middle">
                                <div>
                                    <label class="switch">
                                        <input v-model="item.modelisbillable"
                                            class="form-check-input rdoticketfor custom-control-input dynamic clschkBillable"
                                            data-change-id="no" type="checkbox" id="chkBillable"
                                            onclick="javascript:checkbillable(this);" tabindex="5" :class="{toggled: isToggled}" @click="isToggled = !isToggled">
                                        <span class="slider round off">
                                            <span class="slider-yes">Yes</span>
                                            <span class="slider-no">No</span>
                                        </span>
                                    </label>
                                </div>


                            </td>
                            <td class="align-middle">
                                <v-date-picker v-bind:name="fromDate" v-model="item.modeldate" :minute-increment="5"
                                    class="timedate input-group" :maxDate="new Date()">
                                    <template v-slot="{ inputValue, inputEvents, togglePopover }">
                                        <input placeholder="M/D/YYYY" class="form-control" :value="inputValue"
                                            v-on="inputEvents" />
                                        <div class="input-group-append" v-on:click="togglePopover">
                                            <span class="input-group-text">
                                                <em class="fa fa-calendar"></em>
                                            </span>
                                        </div>
                                    </template>
                                </v-date-picker>
                            </td>
                            <td class="align-middle d-none">
                                <select id="ddlShift" class="form-control" onchange="javascript:ddlShiftChange(this);"
                                    name="Users">
                                    <option value="0">Select Shift</option>
                                    <option value="1">Full Day</option>
                                    <option value="2">Half Day</option>
                                </select>
                            </td>
                            <td class="align-middle">
                                <input id="TotalTimeText" v-model="modeltotalhours" class="form-control" placeholder="00:00"
                                    onblur="javascript:TotalTimeChange(this);"
                                    onclick="javascript:BlankText(this);" onkeypress="javascript:onlynumeric(event,this);"
                                    onpaste="javascript:onlynumeric(event,this);" maxlength="5">
                                <span id="totalTimeTextError" class="text-danger"></span>
                            </td>

                            <td class="align-middle d-none">
                                <div id="divStarttime" class="Starttime">
                                    <input class="form-control btn-time text-center" type="text" id="txtStartTime"
                                        disabled="disabled" placeholder="H:MM TT" onblur="javascript:ChangeStartTime(this);"
                                        autocomplete="off">

                                </div>
                                <div id="divStarttime24 d-none" style="display: none;">
                                    <input class="form-control btn-info text-center" name="" type="text"
                                        placeholder="HH:MM:SS" id="txtStartTime24" onblur="javascript:AddTimeBlur(this);"
                                        onfocus="javascript:onFocus(this);" autocomplete="off">
                                </div>
                            </td>
                            <td class="align-middle d-none">
                                <div id="divStoptime" class="Starttime">
                                    <input class="form-control btn-time text-center" type="text" id="txtStopTime"
                                        placeholder="H:MM TT" disabled="disabled" autocomplete="off">
                                    <span id="stoptimeError" class="text-danger"></span>
                                </div>
                                <div id="divStoptime24" style="display: none;">
                                    <input class="form-control btn-info text-center" name="input" type="text"
                                        placeholder="HH:MM:SS" id="txtStopTime24" onblur="AddTimeBlur(this);"
                                        onfocus="onFocus(this);" autocomplete="off">
                                </div>
                            </td>
                            <td class="text-center align-middle single-action">
                                <a title="" href="javascript:;" class="p-1" v-if="isShowing" v-on:click="copyArray(item)"
                                    id="aEntryClone" data-toggle-tooltip="tooltip" data-original-title="Clone"><i
                                        class="fa fa-copy action-icon"></i></a>

                                <a title="" href="javascript:;" class="p-1" v-on:click="resetArray(index)"
                                    data-toggle-tooltip="tooltip" data-original-title="Clear all "><i
                                        class="fa fa-undo text-danger action-icon"></i></a>
                                <a title="" href="javascript:;" class="p-1"
                                    id="aEntryDelete" data-toggle-tooltip="tooltip" data-original-title="Delete"  v-on:click="deleteItem(index)"><i
                                        class="fa fa-times-circle text-danger action-icon"></i></a>
                            </td>
                        </tr>
                        <tr class="overflowhidden innerwhitebase d-none" id="overlapdiv" style="display: table-row;">
                            <td colspan="11" class="bulkgroupbg widthauto">
                                Loading.....
                                <input type="hidden" id="hdnUserType" value="CA">
                            </td>

                        </tr>
                    </tbody>
                </table>
            </div>
            
        </div>
        <div class="row flex-row-reverse mt-2 visible" id="divaddnew" v-if="isShowing">
                    <div class="col-lg-6 text-right ">
                        <a id="aAddnewLine" @click="addRowArray();" title="" class="btn btn-primary formbtn p-1" data-toggle-tooltip="tooltip" data-original-title="Click here to add new row."><i class="fa fa-plus pr-2"></i>Add</a>
                        <a id="btnSave" title="" @click="SubmitBulkTimeEntry();" class="btn btn-success formbtn widthhalf text-white p-1" data-toggle-tooltip="tooltip" data-original-title="Click here to save record"><i class="fa fa-save pr-2"></i>Save</a>
                        <a id="btnReset" title="" @click="resetRowArray();" class="btn btn-danger formbtn widthhalf p-1" data-toggle-tooltip="tooltip" data-original-title="Click here to reset screen"><i class="fa fa-refresh pr-2"></i>Reset</a>
                    </div>
            </div>
    </section>
</template>
<script>
import DataService from '../../services/DataService';
import Multiselect from 'vue-multiselect'
export default {
    components: {
        Multiselect
    },
    data() {
        return {
            isLoading: false,
            projects: [],
            modelprojectid: null,
            TaskData: [],
            value: [],
            UserList:[],
            isShowing:false,
            isHiding:true,
            options:[],
            multiselectArray: [],
            dynamicArray: [
                {
                    modelprojectid: '',
                    modeltaskid: '',
                    modeluserid: '',
                    modeldescription: '',
                    modelisbillable: '',
                    modeldate: new Date(),
                    modeltotalhours: '',
                    count: 1
                }
            ],
            isToggled:false
        }
    },
    created() {
        this.getProjects();
        this.getUserByProjectIdAndTaskId(0,0);
    },
    methods: {
        async getProjects() {
            var vm = this;
            vm.isLoading = true;
            await DataService.GetTimerProjectsByUserID()
            .then(function (response) {
                vm.projects = response.data.res;
            });
        },
        async ddlProjectschange(Pid) {
            var vm = this;
            vm.isLoading = true;
            var params = `projectId=${Pid}&userId=0&isFor=bulktimeentry`;
            await DataService.LoadTask(params)
            .then(function (response) {
                vm.TaskData = response.data;
            });
        },
        async getUserByProjectIdAndTaskId(Pid,TaskId) {
            var vm = this;
            vm.isLoading = true;
            var params = `projectId=${Pid}&taskId=${TaskId}`;
            await DataService.getUserByProjectIdAndTaskId(params)
            .then(function (response) {
               var userList = response.data;
               userList.forEach((item,index)=>{
                    var obj ={
                     value : item.id,
                     name :  item.strUserName
                    }
                    vm.options.push(obj);
                });
            });
        },
        createMultiRows(gettablerows) {
            debugger
            if(gettablerows>=0)
            {
            var vm = this;
            var temparray = [];
            vm.dynamicArray[0].modeluserid.forEach((item,index)=>{
                if(index > 0){
                    temparray.push(item);
                }
            })
            vm.dynamicArray[0].modeluserid.splice(1);
            for (let i = 0; i < gettablerows; i++) {
                vm.dynamicArray.push({
                    modelprojectid: vm.dynamicArray[i].modelprojectid,
                    modeltaskid: vm.dynamicArray[i].modeltaskid,
                    modeluserid: temparray[i],
                    modeldescription: vm.dynamicArray[i].modeldescription,
                    modelisbillable: vm.dynamicArray[i].modelisbillable,
                    modeldate: vm.dynamicArray[i].modeldate,
                    modeltotalhours: '',
                    count:+1
                })
            }
            vm.isShowing = true;
            vm.isHiding = false;
        }
        },
        copyArray(value){
            var vm = this;
            var obj ={
                modelprojectid : value.modelprojectid,
                    modeltaskid: value.modeltaskid,
                    modeluserid: value.modeluserid,
                    modeldescription: value.modeldescription,
                    modelisbillable: value.modelisbillable,
                    modeldate: value.modeldate,
                    modeltotalhours: value.modeltotalhours,
                    count:+1
            }
            vm.dynamicArray.push(obj);
        },
        addRowArray(){
            var vm = this;
            var obj ={
                modelprojectid : '',
                    modeltaskid: '',
                    modeluserid: '',
                    modeldescription: '',
                    modelisbillable: '',
                    modeldate: new Date(),
                    modeltotalhours: '',
                    count:+1
            }
            vm.dynamicArray.push(obj);
        },
        resetRowArray(){
            var vm = this;
            vm.dynamicArray =  [];
            var obj ={
                modelprojectid : '',
                    modeltaskid: '',
                    modeluserid: '',
                    modeldescription: '',
                    modelisbillable: '',
                    modeldate: new Date(),
                    modeltotalhours: '',
                    count:+1
            }
            vm.dynamicArray.push(obj);
            vm.isShowing = false;
            vm.isHiding = true;

        },
        resetArray(rowindex){
            debugger
            var vm = this;
            vm.dynamicArray.forEach((getArray,getArrayIndex)=>{
               debugger
               if(rowindex==getArrayIndex)
               {
                 vm.dynamicArray[rowindex].modelprojectid= '';
                 vm.dynamicArray[rowindex].modeltaskid= '';
                 vm.dynamicArray[rowindex].modeluserid= '';
                 vm.dynamicArray[rowindex].modeldescription= '';
                 vm.dynamicArray[rowindex].modelisbillable= '';
                 vm.dynamicArray[rowindex].modeldate= '';
                 vm.dynamicArray[rowindex].modeltotalhours= '';
                }
            })
        },
        deleteItem(index) {
            var vm = this;
            if(vm.dynamicArray.length==1)
            {
                vm.ShowAlert(vm.$t('DeletedErrorBulkEntry', [vm.$t('val')]), "failure", true, vm.$t('Alert'));
            }
            else{
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("ApplicationId")), function () {
                vm.dynamicArray.splice(index, 1);
                });
            }
        
        },
        async SubmitBulkTimeEntry() {

            var JsonArry = [];
            var vueObj = this;
            vueObj.dynamicArray.forEach((item,index)=>{
            var tempmodeluserid = 0;
            if(index==0)
            {
                tempmodeluserid = item.modeluserid[0].value
            }
            else
            {
                tempmodeluserid = item.modeluserid.value       
            }
            
            var jsonobj = {
                project_id: item.modelprojectid,
                bulk_track_id:item.count,
                task_id: item.modeltaskid,
                user_id: tempmodeluserid,
                track_description: item.modeldescription,
                track_start_date: item.modeldate,
                is_billable:item.modelisbillable,
                starttimeseconds:'',
                track_hours: item.modeltotalhours
            };
            JsonArry.push(jsonobj);			
        });
        var params = JSON.stringify(JsonArry);
        await DataService.SubmitBulkTimeEntry(params).then((response) => {
            debugger
            if (response.data.result.MSG == 'SUCCESS') {
                vueObj.ShowAlert(vueObj.$t("UpdatedSuccess",[vueObj.$t("Status")]), "success", true, vueObj.$t("Alert"));   
                vueObj.Close();                                   
            }
            else {
                vueObj.ShowAlert(vm.$t("ErrorWhileSavingRecords", [vueObj.$t("Status")]), "failure", true, vueObj.$t("Alert"));
                vueObj.Close();
            }
        });
        }     
    }
}
</script>